.menu-menu{  
    padding: 0;   
      
}

.menu-ul{
    display: flex;
    text-align: center;
    background-color: #19a582;
    width: 80%;
    padding: 0;
    margin: 0 auto;
    border-radius: 10px;
}
Link{

    text-decoration: none;
}

.menu-ul li{
    list-style: none;   
    margin: 0 auto;      
    padding: 15px;
    border-radius: 15px;
    color: white; 
    transition: all 500ms ease;
    font-family: 'Lora', serif;
    font-size: 22px;
    text-decoration: none;
              
}

.li-vamos{
    margin: 0 auto;  
    text-decoration: none;
       
}

.menu-ul li:hover{

color: rgb(57, 57, 228);


}

.menu-imagem{    
    width: 80%;
    height: 400px   ;
    border-radius: 10px;
    display: flex;   
    margin: 0 auto
}